/**
 *      Rapier.js
 */

//     RAPIER  
const MAX_WAIT_TIME = 10000;

/**
 *  Rapier.js
 * @returns {Promise} - ,  ,  Rapier.js 
 */
export async function initRapier() {
    console.log('  Rapier.js...');
    
    // ,    RAPIER
    if (typeof window !== 'undefined' && window.RAPIER_LOADER) {
        console.log('  RAPIER_LOADER...');
        try {
            const rapier = await window.RAPIER_LOADER.loadRapier();
            console.log('RAPIER_LOADER   Rapier');
            return rapier;
        } catch (error) {
            console.error('   RAPIER_LOADER:', error);
            throw error;
        }
    }
    
    //   ,   
    // ,    Rapier.js
    if (typeof RAPIER !== 'undefined') {
        // ,   RAPIER  
        if (RAPIER._initialized) {
            console.log('Rapier.js  ');
            return RAPIER;
        }
        
        //  RAPIER ,   ,  
        if (typeof RAPIER.init === 'function') {
            try {
                console.log(' Rapier.js  RAPIER.init()');
                await RAPIER.init();
                RAPIER._initialized = true;
                console.log('Rapier.js  ');
                return RAPIER;
            } catch (error) {
                console.error('   Rapier.js:', error);
                throw error;
            }
        } else {
            //  init  , ,     RAPIER
            console.log('Rapier.js     ');
            RAPIER._initialized = true;
            return RAPIER;
        }
    }
    
    try {
        // ,   RAPIER  window
        if (typeof window !== 'undefined' && window.RAPIER) {
            //  RAPIER   window, ,    
            if (typeof window.RAPIER.init === 'function' && !window.RAPIER._initialized) {
                console.log(' Rapier.js  window.RAPIER');
                await window.RAPIER.init();
                window.RAPIER._initialized = true;
                console.log('window.RAPIER  ');
            } else {
                console.log('  Rapier.js  window.RAPIER');
                window.RAPIER._initialized = true;
            }
            return window.RAPIER;
        }
        
        console.log('Rapier.js      ,  ...');
        
        // ,  RAPIER      
        return await waitForRapierWithTimeout(MAX_WAIT_TIME);
    } catch (error) {
        console.error('   Rapier.js:', error.message);
        
        //      
        return createRapierStub();
    }
}

/**
 *     RAPIER  
 * @param {number} maxWaitTime -     
 * @returns {Promise} - ,     RAPIER    
 */
async function waitForRapierWithTimeout(maxWaitTime) {
    return new Promise((resolve, reject) => {
        let attempts = 0;
        const maxAttempts = 20;
        const checkInterval = 300; // ms
        const startTime = Date.now();
        
        const checkRapier = () => {
            attempts++;
            const currentTime = Date.now();
            
            // ,    
            if (currentTime - startTime > maxWaitTime) {
                reject(new Error(`   RAPIER (${maxWaitTime}ms)`));
                return;
            }
            
            //   
            if (typeof window !== 'undefined' && window.RAPIER_LOADER) {
                window.RAPIER_LOADER.loadRapier()
                    .then(rapier => {
                        console.log('RAPIER   RAPIER_LOADER');
                        resolve(rapier);
                    })
                    .catch(error => {
                        console.error('   RAPIER_LOADER:', error);
                        
                        //    
                        setTimeout(checkRapier, checkInterval);
                    });
                return;
            }
            
            //   RAPIER
            if (typeof RAPIER !== 'undefined') {
                if (typeof RAPIER.init === 'function' && !RAPIER._initialized) {
                    console.log(`RAPIER ,   ( ${attempts})`);
                    RAPIER.init().then(() => {
                        console.log('RAPIER  ');
                        RAPIER._initialized = true;
                        resolve(RAPIER);
                    }).catch(error => {
                        console.error('  RAPIER:', error);
                        reject(error);
                    });
                    return;
                } else {
                    console.log(`RAPIER    ( ${attempts})`);
                    RAPIER._initialized = true;
                    resolve(RAPIER);
                    return;
                }
            }
            
            //  window.RAPIER
            if (typeof window !== 'undefined' && window.RAPIER) {
                if (typeof window.RAPIER.init === 'function' && !window.RAPIER._initialized) {
                    console.log(`window.RAPIER ,   ( ${attempts})`);
                    window.RAPIER.init().then(() => {
                        console.log('window.RAPIER  ');
                        window.RAPIER._initialized = true;
                        resolve(window.RAPIER);
                    }).catch(error => {
                        console.error('  window.RAPIER:', error);
                        reject(error);
                    });
                    return;
                } else {
                    console.log(`window.RAPIER    ( ${attempts})`);
                    window.RAPIER._initialized = true;
                    resolve(window.RAPIER);
                    return;
                }
            }
            
            if (attempts >= maxAttempts) {
                console.warn(`RAPIER    ${maxAttempts} ,      ...`);
            }
            
            setTimeout(checkRapier, checkInterval);
        };
        
        checkRapier();
    });
}

/**
 *    Rapier.js
 * @returns {Object} -    
 */
function createRapierStub() {
    console.warn('  Rapier.js.     !');
    
    return {
        _initialized: true,
        World: class StubWorld {
            constructor() {
                console.warn('  World   Rapier.js');
                this.bodies = [];
                this.colliders = [];
            }
            
            step() {
                console.log('    ');
            }
            
            createRigidBody() {
                return {
                    translation: () => ({ x: 0, y: 0, z: 0 }),
                    setTranslation: () => {}
                };
            }
            
            createCollider() {
                return {};
            }
            
            castRay() {
                return null;
            }
            
            castRayAndGetNormal() {
                return { hasHit: false, toi: 0 };
            }
        },
        RigidBodyDesc: {
            fixed: () => ({
                setTranslation: () => ({})
            }),
            dynamic: () => ({
                setTranslation: () => ({}),
                setCanSleep: () => ({}),
                setLinearDamping: () => ({})
            })
        },
        ColliderDesc: {
            cuboid: () => ({}),
            capsule: () => ({})
        },
        Ray: class StubRay {
            constructor(origin, dir) {
                this.origin = origin;
                this.dir = dir;
            }
        }
    };
}

/**
 *   Rapier.js
 * @returns {boolean} -  
 */
export function isRapierAvailable() {
    //   ,   
    if (typeof window !== 'undefined' && window.RAPIER_LOADER) {
        return window.RAPIER_LOADER.isRapierReady();
    }
    
    //  
    return (typeof RAPIER !== 'undefined' && RAPIER._initialized) || 
           (typeof window !== 'undefined' && window.RAPIER && window.RAPIER._initialized);
}

/**
 *   Rapier.js
 * @returns {Object|null} -  Rapier.js  null,    
 */
export function getRapier() {
    //   ,   
    if (typeof window !== 'undefined' && window.RAPIER_LOADER) {
        return window.RAPIER_LOADER.getRapier();
    }
    
    //  
    if (typeof RAPIER !== 'undefined' && RAPIER._initialized) {
        return RAPIER;
    }
    
    if (typeof window !== 'undefined' && window.RAPIER && window.RAPIER._initialized) {
        return window.RAPIER;
    }
    
    return null;
}